import React, { Component } from 'react';
import todosData from './components/Todo-Component/TodoData';
import TodoItem from './components/Todo-Component/TodoItem';
import TodoMenu from './components/Todo-Component/TodoMenu';

class App extends Component {
  constructor() {
    super();
    this.state = {
      todos: todosData,
      warning: false,
    };

    this.handleChange = this.handleChange.bind(this);
    this.AddItem = this.AddItem.bind(this);
    this.removeTodoitem = this.removeTodoitem.bind(this);
  }

  handleChange(id) {
    this.setState((prevState) => {
      const updatedTodos = prevState.todos.map((todo) => {
        if (todo.id === id) {
          todo.completed = !todo.completed;
        }

        return todo;
      });

      return {
        todos: updatedTodos,
      };
    });
  }

  removeTodoitem(id) {
    this.setState((prevState) => {
      prevState.todos = prevState.todos.filter((x) => x.id !== id);
      return prevState;
    });
  }

  AddItem(text) {
    text = text.trim();

    if (text === '') {
      this.setState(prevState => {
        return {
          warning: true
        }
      });
      return;
    }

    const lastId = this.state.todos.length !== 0 ? this.state.todos[ this.state.todos.length - 1].id : 0;
    const newTodoItem = {
      id: lastId + 1,
      todo: text,
      completed: false,
    };

    this.setState((prevState) => {
      prevState.todos.push(newTodoItem);
      return {
        todos: prevState.todos,
        warning: false
      };
    });
  }

  render() {
    const todoItems = this.state.todos.map((item) => (
      <TodoItem
        key={item.id}
        item={item}
        removeTodoitem={this.removeTodoitem}
        handleChange={this.handleChange}
      />
    ));

    return (
      <div className="todo-list">
        <TodoMenu warning={this.state.warning} AddItem={this.AddItem} />
        {todoItems}
      </div>
    );
  }
}

export default App;
